.gpt3__header{
   
   display: flex;
   flex-direction: row;
}
.gpt3__header-content{
    flex: 1;
    display: flex;
    justify-content: center;
    align-items: flex;
    flex-direction: column;
     
    margin-top: 0;
    margin-right: 8rem;
}
.gpt3__header-content h1{
    font-family: var(--font-family);
    font-weight: 800;
    font-size: 55px;
    line-height: 65px;
    letter-spacing: -0.04em;
    margin-top: 0;
    padding: 0;

}
.gpt3__header-content p{

    font-family: var(--font-family);
    font-style: normal;
    font-weight: normal;
    font-size: 15px;
    line-height: 20px;
    color: var(--color-text);

    margin-top: 1.5rem;
}
.gpt3__header-content__input{

    width: 100%;
    margin: 1rem 0 1rem;

    display: flex;
}
.gpt3__header-content__input input{

    flex: 2;
    width: 100%;
    min-height: 50px;
    background: var(--color-footer);
    border: 2px solid var(--color-footer);
    padding: 0 1rem;
    outline: none;
    color: #fff;
    font-family: var(--font-family);
    font-size: 20px;
    line-height: 27px;
    border-top-right-radius: 5px;
    border-bottom-right-radius: 5px;
  
}
.gpt3__header-content__input button{
    
    flex: 0.6;
    width: 100%;
    min-height: 50px;
    background: #FF4820;
    font-family: var(--font-family);
    font-weight: bold;
    font-size: 20px;
    line-height: 27px;
    color: #FFFFFF;
    cursor: pointer;
    outline: none;
    padding: 0 1rem;
    border: none;

    border-top-right-radius: 5px;
    border-bottom-right-radius: 5px;

}
.gpt3__header-content__people{
    width: 100%;
    display: flex;
    justify-content:flex-start ;
    align-items: center;

    margin-top: 1rem;
}
.gpt3__header-content__people img{

    width: 181.79px;
    height: 38px;

}
.gpt3__header-content__people p{


    margin: 0 0 0 1rem;
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 12px;
    line-height: 38px;
    text-align: center;
    color: #FFFFFF;
}
.gpt3__header-image {

    flex: 1;
    display: flex;
    justify-content: center;
    align-items: center;

}
.gpt3__header-image img{
    width: 100%;
    height: 100%;
    margin: 0;
}
@media screen and (max-width: 1050px){

    .gpt3__header{
        flex-direction: column;
    }
    .gpt3__header-content{

        margin:  0 0 3rem;
    }

}
@media screen and (max-width: 650px){

    .gpt3__header h1{
       font-size: 47px;
       line-height: 60px;
    }
    .gpt3__header p{
        font-size: 16px;
        line-height: 24px;
     }
    
    .gpt3__header-content__people{
        flex-direction: column;
    }
    .gpt3__header-content__people p{
      margin: 0;
    }
    .gpt3__header-content__input input,
    .gpt3__header-content__input button{

        font-size: 16px;
        line-height: 24px;
    }

}
@media screen and (max-width: 490px){
    
    .gpt3__header h1{
        font-size: 36px;
        line-height: 48px;
     }
     .gpt3__header p{
         font-size: 14px;
         line-height: 24px;
      }
      .gpt3__header-content__input input,
      .gpt3__header-content__input button{
  
          font-size: 12px;
          line-height: 16px;
      }
}